import React from 'react';
import { ListView, Button,  InputItem, } from 'antd-mobile';
import { injectIntl } from 'react-intl';
import { connect } from 'dva';
import Auto from '../utils/Auto'
import {routerRedux} from 'dva/router';
import Constants from '../utils/Constants'
require('moment/locale/zh-cn');
var ScreenWidth = window.screen.width 
var ScreenHeight = window.screen.height

class GameDescription extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      lease: this.props.location.query ? this.props.location.query.lease : false,
    };
  }

  componentDidMount() {
   
  }

  render() {
    return (<div style={styles.rootDiv}>
      <p style={styles.description}>REX</p>
      <p style={styles.explaintext}>1REX214</p>
      <p style={styles.explaintext}> 2REXEOSEOSREX</p>
      {this.state.lease &&
      <div >
        <p style={styles.descriptiontitle}></p>
        <p style={styles.explaintext}>1CPUNET,
             EOS</p>
      </div>
      }
    </div>
    )
  }
} 

export default connect(({ }) => ({ }))(injectIntl(GameDescription));

const styles = { 
  rootDiv:{
    width: ScreenWidth,
    height: ScreenHeight,
    background:"#F5F5F9",
    boxSizing: 'border-box', 
    padding: Auto.WHT(30),
  },
  description: {
    color: '#000000', 
    fontSize: Auto.WHT(48), 
    lineHeight: Auto.WHT(67), 
    paddingTop: Auto.WHT(10),
  },
  explaintext: {
    color: '#333333', 
    fontSize: Auto.WHT(28), 
    lineHeight: Auto.WHT(36), 
    paddingTop: Auto.WHT(30),
  },
  descriptiontitle: {
    color: '#000000', 
    fontSize: Auto.WHT(48), 
    lineHeight: Auto.WHT(67), 
    paddingTop: Auto.WHT(70),
  },
}



